import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import {Button, Table} from 'reactstrap';
import IntlMessages from 'util/IntlMessages';
import DataTableCell from '../table/routes/basic/Components/DataTableCell';
import { userList } from 'actions/UserList';


class UserList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            userList: [],
            showData: false,
            SN: 0
        }
    }

    componentDidMount(){
        this.props.userList();
       // console.log(this.props.match.url);
    }

    render(){


        return (
            <div className={'faqMainContainer'}>
            <div className={'faqContainer'}>
                <div className="row">
                    <div className="col-12">
                        <div className="jr-card">
            <div className="jr-card-header d-flex align-items-center">
                {/*<h3 className="mb-0"><IntlMessages id="faq.category"/></h3>*/}
                {/*<div className="ml-3">*/}
                                {/*<span className="text-white badge badge-success">*/}
                                    {/*<IntlMessages id="faq.myUser"/></span>*/}
                {/*</div>*/}
            </div>
            <div className="table-responsive-material">
                <Table>
                    <thead>
                    <tr>
                        <th className="border-top-0">S/N</th>
                        <th className="border-top-0">Names</th>
                        <th className="border-top-0">Email</th>
                        <th className="border-top-0">Phone Number</th>
                        <th className="border-top-0">Loan Level</th>
                        <th className="border-top-0">Gender</th>
                        <th className="border-top-0">User Category</th>
                        <th className="border-top-0">Action</th>
                    </tr>
                    </thead>
                    <tbody>

                {this.props.user.userList.map((item, index) =>  {
                    return (
                        <tr key={item.id}>

                              <td>{index + 1}</td>
                              <td>{item.last_name} {item.other_names}</td>
                              <td>{item.email}</td>
                              <td>{item.phone_number}</td>
                              <td>{item.loan_level}</td>
                              <td>{item.gender}</td>
                              <td>{item.user_category}</td>
                            <td>
                                <div>
                                    <Link to={this.props.match.url + "/" + item.phone_number}>
                                        <Button className="jr-btn btn-primary text-white mb-0" style={{backgroundColor: '#0066f5'}}>
                                            <i className="zmdi zmdi-eye" style={{paddingRight: 5}}/>
                                            <span>View Profile</span>
                                        </Button>
                                    </Link>
                                </div>

                            </td>
                        </tr>
                    );
                })}
                    </tbody>
                </Table>
            </div>
                        </div>
                    </div>
                </div>
            </div>
            </div>
            );
    }
}

const mapStateToProps = (state) => ({
    user: state.userList
})



export default connect(mapStateToProps, {userList})(UserList);


